@import '../../gacolors.scss';

$radius: 6rem;

.thumbnail {
    display: flex;
    justify-content: center;
    align-items: center;

    width: $radius;
    height: $radius;
    border-radius: 50%;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.05), 0 3px 6px rgba(0, 0, 0, 0.05);
    margin-bottom: 1rem;
}

.thumbnail-placeholder {
    background-color: $ga-lighter-gray;

    limel-icon {
        height: 3rem;
        width: 3rem;
    }
}
