import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'profile-picture',
    shadow: true,
    styleUrl: 'profile-picture.scss',
})
export class LayoutSettings {
    @Prop() public thumbUrl: string;

    public render() {
        if (this.thumbUrl) {
            return (
                <img class="thumbnail" src={this.getThumbUrl(this.thumbUrl)} />
            );
        }

        return (
            <div class="thumbnail thumbnail-placeholder">
                <limel-icon name="user" />
            </div>
        );
    }

    private getThumbUrl(originalUrl = '') {
        const path: string = originalUrl.split('/').slice(-1)[0];
        const urlPath: string = originalUrl.split('/')[3];

        return `getaccept/thumb_proxy/${urlPath}/${path}`;
    }
}
